import Mock from 'mockjs';
Mock.mock('/api/message', 'get', {
    code: 0,
    msg: "",
    data: {
        total: 50,
        "rows|10": [{
            'id|+1': 1,
            nickname: "@id",
            "content|1-20": "@ctitle",
            createDate: '@date("T")',
            avatar: "@image(100x100,@color,@color,@ctitle)",
        }, ],
    }
})
Mock.mock('/api/message', 'post', {
    code: 0,
    msg: "",
    data: {
        "id|+1": 1,
        nickname: "@cname",
        "content|1-20": "@ctitle",
        createDate: '@date("T")',
        avatar: "@image(100x100,@color,@color,@ctitle)",
    }
})