export default function(typeDefault = null) {
    return {
        data() {
            return {
                data: typeDefault, //  
                isLoading: true,
            }
        },
        async created() { //promise
            this.data = await this.feachData();
            this.sortData();
            this.isLoading = false;
        },
        methods: {
            sortData() {
                if (this.data.rows) {
                    this.data.rows.sort((a, b) => {
                        return b.createDate - a.createDate
                    })
                }
            },
        }
    }
}