<template>
  <div class="menu-container">
    <small v-if="data.siteTitle">{{ user?user.loginId : data.siteTitle }}<span></span></small>
    <RouterLink
      :exact="item.exact"
      v-for="item in items"
      :key="item.name"
      :to="{ name: item.name }"
    >
      <Icon :type="item.icon" />{{ item.title }}
    </RouterLink>
  </div>
</template>

<script>
import Icon from "@/components/icon";
import { mapState } from "vuex";
import { mapGetters } from "vuex";
export default {
  name: "Menu",
  components: {
    Icon,
  },
  data() {
    return {
      items: [
        {
          exact: true, //
          name: "Home",
          title: "",
          icon: "home",
        },
        {
          exact: false, //
          name: "Blog",
          title: "",
          icon: "blog",
        },
        {
          exact: true, //
          name: "About",
          title: "",
          icon: "xinxi",
        },
        {
          exact: false, //
          name: "Project",
          title: "&",
          icon: "code",
        },
        {
          exact: true, //
          name: "Message",
          title: "",
          icon: "liuyan",
        },
        {
          exact: true, //
          name: "Person",
          title: ` - `,
          icon: "person",
        },
      ],
    };
  },
  computed: {
    ...mapState("global", ["data"]),
    ...mapState("person", ["user"]),
    ...mapGetters("person",["status"]),
    // status(){
    //     this.items[this.items.length - 1].title = ` - ${this.$store.getters['person/status']}`
    //     return this.$store.getters['person/status']
    // }
  },
  watch: {
      status(val,oldVla){
            this.items[this.items.length - 1].title = ` - ${val}`
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/styles/hunhe.less";
@import "~@/styles/global.less";
.menu-container {
  small {
    text-align: center;
    margin-bottom: 0.5rem;
    color: rgb(189, 165, 165);
    cursor: pointer;
    span {
      color: @gray;
    }
  }
  background-color: transparent;
  .selfFill(relative);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  a {
    height: 20%;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding-left: 2rem;
    font-size: 12px;
    color: @gray;
    .iconfont-container {
      font-size: 14px;
      color: @gray;
      margin-right: 1rem;
    }
    a {
      margin-left: 1rem;
    }
    &:hover {
      background-color: @dark;
    }
    &.router-link-active {
      background-color: #2d2d2d;
    }
  }
}
</style>