<template>
  <form class="login-container" @submit.prevent="handleSubmit">
    <div>
      <span :class="{active:isShow}" @click="handleClick"></span>
      <span :class="{active:!isShow}" @click="handleClick"></span>
    </div>
    <div class="loginName" v-if="isShow">
      <div class="name">
        <input
          v-model="user.userName"
          id="userName"
          type="text"
          placeholder="//"
        />
      </div>
      <div class="passWord">
        <input
          v-model="user.passWord"
          id="passWord"
          type="password"
          placeholder=""
        />
      </div>
      <div>
        <button></button>
      </div>
    </div>
    <div class="loginCode" v-if="!isShow">
      <img :src="codeSrc" alt="" />
    </div>
    <div>
      <p>
        <span class="text"></span> 
        <span class="text"></span>
      </p>
    </div>
    <div>
      <span>admin 123123</span>
    </div>
  </form>
</template>

<script>
function delay() {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve();
    }, 500);
  });
}
export default {
  data() {
    return {
      codeSrc: "https://img.wenhairu.com/images/2022/01/20/HEBjd.md.jpg",
      isShow: true,
      farst: null,
      user: {
        userName: "",
        passWord: "",
      },
    };
  },
  methods: {
    async handleSubmit() {
      const targetUrl = this.$route.query.targetUrl;
      this.$router.push({ name: "Loading", query: { targetUrl } });
      const resp = await this.$store.dispatch("person/feachLogin", this.user);
      if (!resp) {
        //
        this.user = {};
        alert("");
      }
    },
    handleClick(event) {
      const targetDom = event.target;
      if (this.farst !== targetDom) {
        //
        this.farst = targetDom;
        this.isShow = !this.isShow;
      }
    },
  },
  created() {
  }
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.login-container {
  height: 50%;
  width: 333px;

  margin: 100px auto;
  div {
    margin: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    &.loginName {
      display: flex;
      flex-direction: column;
      div {
        width: 100%;
        input {
          flex: 1 1 auto;
          border: 1px solid rgb(202, 198, 198);
          outline: none;
          border-radius: 8px;
          height: 30px;
          padding: 8px;
        }
        button {
          outline: none;
          border: none;
          cursor: pointer;
          height: 40px;
          font-size: 16px;
          width: 100%;
          border-radius: 8px;
          background: rgb(151, 192, 178);
        }
      }
    }
    &.loginCode {
      height: 100%;
      width: 100%;
      img {
        height: 60%;
        width: 60%;
      }
    }
    .text {
      color: @primary;
      cursor: pointer;
    }
    & > span {
      font-size: 18px;
      margin: 0 10px;
      cursor: pointer;
      &.active {
        position: relative;
        &:after {
          display: block;
          content: "";
          height: 2px;
          width: 100%;
          background: @primary;
          position: absolute;
        }
      }
    }
  }
}
</style>