function delay(times) {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve()
        }, times)
    })
}

import 'nprogress/nprogress.css'
import { start, done, configure } from 'nprogress'

configure({
    trickleSpeed: 20, //
    showSpinner: false, //
})

start() //
done() //
function getPageComponent(page) { //Promise
    return async() => {
        start();
        if (process.env.NODE_ENV !== 'production') { //
            await delay(2000);
        }
        const component = await page();
        done();
        return component
    }
}
import Home from '@/views/Home';
import About from '@/views/About';
import Demo from '@/views/Project/Demo';
import Project from '@/views/Project';
import Blog from '@/views/Blog';
import Details from '@/views/Blog/components/Details';
import Message from '@/views/Message';
import Person from '@/views/Person';
import Login from '@/components/Login';
import Loading from '@/views/Loading';
const routes = [{
        name: 'Home',
        path: '/',
        component: Home,
        meta: { title: '', }
    },
    {
        name: 'About',
        path: '/about',
        component: About,
        meta: { title: '', }
    },
    {
        name: 'Demo',
        path: '/project/demo/:url',
        component: Demo,
        meta: { title: '', }
    },
    {
        name: 'Project',
        path: '/project',
        component: Project,
        meta: { title: '', }
    },
    {
        name: 'Blog',
        path: '/article',
        component: Blog,
        meta: { title: '', }
    },
    //
    {
        name: 'CategoryBlog',
        path: '/article/cate/:categoryId',
        component: Blog,
        meta: { title: '', }
    },
    {
        name: 'Blogdetails',
        path: '/article/:detailId',
        component: Details,
        meta: { title: '', }
    },

    {
        name: 'Message',
        path: '/message',
        component: Message,
        meta: {
            title: '',
            auth: true,
        }
    },
    {
        name: 'Person',
        path: '/person',
        component: Person,
        meta: {
            title: '',
            auth: true,
        }
    },
    {
        name: 'Login',
        path: '/loginUser',
        component: Login,
        meta: {
            title: '',
        }
    },
    {
        name: 'Loading',
        path: '/loading',
        component: Loading,
        meta: {
            title: '',
        }
    },
]
export default routes;