import request from './request';
//
export async function getBlogs(page = 1, limit = 10, categoryid = -1) {
    return await request.get("/api/blogs", {
        params: {
            page,
            limit,
            categoryid,
        },
    });
}
//
export async function getBlogTypes() {
    return await request.get("/api/blogtypes");
}

//
// path: /api/blog/:id
// method: GET
export async function getBlogType(id) {
    return await request.get(`/api/blog/${id}`, );
}

//
// path: /api/comment
// method: POST
// body: { 
// 	nickname: "",
// 	content: "",
// 	blogId: <id>	#id
// }
export async function postComment(comment) {
    return await request.post("/api/comment", comment)
}

//
// path: /api/comment
// method: GET
export async function getComment(blogid = -1, page = 1, limit = 10) {
    return await request.get("/api/comment", {
        params: {
            page,
            limit,
            blogid
        }
    })
}